/* Training page independent styles */
.fancybox-slide--video .fancybox-content {
	padding-bottom: 62.5%; /* 16: */
}
abbr[title] {
	text-decoration: none;
}
.q-body {
	font-family: Gotham, sans-serif;
}
.q-body h1,
.q-body h2,
.q-body h3,
.q-body h4,
.q-body h5,
.q-body h6 {
	color: #262626;
	font-family: Gotham, sans-serif;
	font-weight: 500;
}
.optional {
	font-size: 0.6em;
}
.q-content-hero__content-inner {
	opacity: 1;
}
.q-content-hero__heading,
.q-content-hero__subheading {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}
.q-content-hero__subheading p{
	font-family: Gotham, sans-serif;
	font-size: 28px;
	line-height: 30px;
	letter-spacing: -0.5px;
	color: #6A778B;
	font-weight: 300;
}
.q-training__header-container {
	display: flex;
	flex-flow: column;
	padding: 30px 0;
	align-items: center;
}
.q-training__header-heading {
	color: #262626;
	font-weight: 400;
	font-size: 40px;
	margin: 0 auto;
	text-align: center;
	letter-spacing: -0.005em;
}
a.q-training__header-block {
	background: #B5D0E5;
	max-width: 300px;
	text-align: center;
	margin: 40px 0 0;
	padding: 16px 30px;
	text-decoration: none;
	transition: background 0.3s;
}
a.q-training__header-block:hover {
	text-decoration: none;
	background: #DDF0FF;
}
.q-training__header-block--heading {
	color: #1D2737;
	margin: 0;
	font-weight: 500;
	font-size: 20px;
}
.q-training__header-block--text {
	margin: 3px 0 0;
	font-family: Gotham, sans-serif;
	color: #1D2737;
	font-weight: 400;
	font-size: 15px;
	line-height: 1.2;
}
@media (min-width: 720px) {
	.q-training__header-container {
		flex-flow: row;
	}
	.q-training__header-heading {
		margin: 0 auto 0 0;
		padding-right: 20px;
		text-align: left;
	}
	.q-training__header-block {
		margin: 0 0 0 auto;
	}
}
.q-training__tabs-container {
	background: #F7FAFC;
	border-top: 1px solid #B5D0E5;
	border-bottom: 1px solid #B5D0E5;
	margin-bottom: 20px;
	margin-top: 18px;
}
.q-training__tabs-list {
	list-style: none;
	list-style-position: inside;
	display: flex;
	flex-flow: column;
	padding: 0;
	margin: 0;
}
.q-training__tabs-list li {
	margin: 0;
	border-bottom: 1px solid #B5D0E5;
}
.q-training__tabs-list li:last-child {
	border-bottom: 0;
}
a.q-training__tabs-item {
	font-family: Gotham, sans-serif;
	padding: 15px 30px;
	display: block;
	font-size: 15px;
	color: #2E8BE0;
	text-decoration: none;
	transition: color 0.3s;
}
a.q-training__tabs-item:hover,
a.q-training__tabs-item:active,
a.q-training__tabs-item:focus {
	text-decoration: none;
	color: #549EE2;
}
a.q-training__tabs-item.q-active-tab {
	color: #FFF;
	background: #2E8BE0;
	font-weight: 500;
}
a.q-training__tabs-item.q-active-tab:hover {
	color: #F7FAFC;
}
@media (min-width: 680px) {
	.q-training__tabs-list {
		flex-flow: row;
	}
	.q-training__tabs-list li {
		border-bottom: 0;
	}
}
.q-tab-hidden {
	display: none;
}

.q-training__items-grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
.q-training__grid-item {
	text-decoration: none;
	margin: 0 0 30px 0;
	width: 100%;
	background: #F7F7F7;
	color: #30405B;
	text-align: center;
	border-radius: 3px;
	transition: all 0.5s;
}
.q-training__grid-item:hover,
.q-training__grid-item:focus {
	text-decoration: none;
}
@media (min-width: 720px) {
	.q-training__items-grid {
		margin-left: -30px;
	}
	.q-training__grid-item {
		margin: 0 0 30px 30px;
		width: calc(50% - 30px);
	}
}
@media (min-width: 980px) {
	.q-training__grid-item {
		width: calc(33.33% - 30px);
	}
}
@media (min-width: 1180px) {
	.q-training__grid-item {
		width: calc(25% - 30px);
	}
}
.q-training__grid-item--wrapper {
	display: block;
	padding: 25px;
	min-height: 16em;
}
.q-training__item-heading {
	font-family: Gotham, sans-serif;
	color: #30405B;
	font-size: 24px;
	margin-bottom: 20px;
	min-height: 3em;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: column;
	letter-spacing: -0.012em;
	transition: color 0.3s;
	line-height: 1.167;
}
.q-training__grid-item:hover .q-training__item-heading {
	color: #FFF;
}
.q-training__item-info {
	font-family: Caecilia, sans-serif;
	color: #6A778B;
	font-size: 17px;
	letter-spacing: -0.009em;
	transition: color 0.3s;
}
.q-training__grid-item:hover .q-training__item-info {
	color: #FFF;
}
.q-training__tabs-section-heading {
	color: #30405B;
	font-size: 17px;
	font-weight: 500;
	margin: 0 0 40px;
	justify-content: center;
	line-height: 1.765;
	letter-spacing: -0.012em;
	max-width: 56.412em;
}
.q-training__tabs-section-subheading {
	color: #262626;
	font-size: 17px;
	font-weight: 500;
	margin: 0 0 20px;
	justify-content: center;
}
h3.q-training__tabs-section-intro {
	font-weight: 400;
}
.q-training__tabs-section-intro {
	color: #30405B;
	font-size: 17px;
	font-weight: 400;
	margin: 0 0 40px;
	justify-content: center;
}
.q-training__tabs-section-intro p {
	margin: 0;
}
.q-training__tabs-section-intro p:not(:first-child) {
	margin-top: 1em;
}
.q-training__tabs-section-intro a {
	color: #2E8BE0;
	text-decoration: none;
}
.q-training__tabs-section-intro a:hover {
	color: #1A77CC;
}

.q-training__grid-item--sequence .q-training__item-heading::before {
	background: #2E8BE0;
	color: #FFF;
	display: block;
	width: 26px;
	height: 26px;
	border-radius: 50%;
	font-size: 15px;
	line-height: 26px;
	margin-bottom: 7px;
}
.q-training__grid-item--sequence:nth-child(1) .q-training__item-heading::before {
	content: "1";
}
.q-training__grid-item--sequence:nth-child(2) .q-training__item-heading::before {
	content: "2";
}
.q-training__grid-item--sequence:nth-child(3) .q-training__item-heading::before {
	content: "3";
}
.q-training__grid-item--sequence:nth-child(4) .q-training__item-heading::before {
	content: "4";
}
.q-training__grid-item--sequence:nth-child(5) .q-training__item-heading::before {
	content: "5";
}
.q-training__grid-item--sequence:nth-child(6) .q-training__item-heading::before {
	content: "6";
}
.q-training__grid-item--sequence:nth-child(7) .q-training__item-heading::before {
	content: "7";
}
.q-training__grid-item--sequence:nth-child(8) .q-training__item-heading::before {
	content: "8";
}
.q-training__grid-item--sequence .q-training__item-heading {
	justify-content: flex-start;
	min-height: 4.5em;
	max-width: 9em;
	margin-left: auto;
	margin-right: auto;
}
.q-training__grid-item--sequence {
	position: relative;
}
.q-training__grid-item:hover {
	color: #FFF;
	background: #1D2737;
}
.q-training__grid-item--sequence::after {
	content: "";
	display: block;
	width: 20px;
	height: 18px;
	background: url(https://ik.imagekit.io/qualys/image/icon-sequence-arrow.svg) 0 0 no-repeat transparent;
	position: absolute;
	bottom: -23px;
	transform: translateX(-50%) rotate(90deg);
	left: 50%;
}
.q-training__items-grid .q-training__grid-item--sequence:last-child::after {
	display: none;
}
@media (min-width: 720px) {
	.q-training__grid-item--sequence::after {
		left: auto;
		right: -27px;
		bottom: auto;
		top: 50%;
		transform: translateY(-50%) rotate(0deg);
	}
	.q-training__grid-item--sequence:nth-child(2)::after,
	.q-training__grid-item--sequence:nth-child(4)::after {
		display: none;
	}
}
@media (min-width: 980px) {
	.q-training__grid-item--sequence:nth-child(2)::after {
		display: block;
	}
	.q-training__grid-item--sequence:nth-child(3)::after {
		display: none;
	}
}
@media (min-width: 1024px) {
	.q-content-hero__heading,
	.q-content-hero__subheading {
		text-align: left;
		margin-left: 0;
		margin-right: 0;
	}
	.q-content-hero__subheading {
		max-width: 20em;
		margin-top: 0;
	}
}
@media (min-width: 1180px) {
	.q-training__grid-item--sequence:nth-child(3)::after {
		display: block;
	}
}
@media (min-width: 1290px) {
	.q-content-hero__subheading p {
		font-size: 36px;
		line-height: 1.2;
		margin-top: 0;
	}
}
.q-training__modal-content {
	display: none !important;
}
.q-training__course-modal {
	width: 770px;
	max-width: 100%;
	background: #1D2737;
	color: #FFF;
	padding-top: 60px;
	padding-bottom: 90px;
}
h3.q-training__modal-heading {
	color: #FFF;
	margin: 0 auto;
	text-align: center;
	max-width: 13.056em;
	line-height: 1.278;
	font-size: 2.250em;
	font-weight: 300;
	letter-spacing: -0.005em;
}
.q-training__modal-course-content {
	max-width: 20.563em;
	text-align: left;
	margin: 45px auto 0;
}
.q-training__modal-subheading {
	font-size: 1.5em;
	color: #FFF;
	line-height: 1.750em;
	letter-spacing: -0.012em;
	font-weight: 300;
	margin: 0 0 18px;
}
.q-training__modal-list ol {
	padding: 0 0 0 1em;
	margin: 0;
	list-style-type: decimal;
}
.q-training__modal-list ol li {
	color: #B5D0E5;
	font-size: 1em;
	letter-spacing: -0.009em;
	font-family: Caecilia, sans-serif;
}
.q-training__modal-list ol li p {
	margin: 0;
}
.q-training__modal-action {
	margin: 60px auto 50px;
}
.q-training__modal-action ul {
	padding: 0;
	margin: 0;
	display: flex;
	flex-direction: column;
	list-style: none;
	justify-content: flex-start;
}
.q-training__modal-action ul li:last-child {
	margin-top: 30px;
}
@media (min-width: 480px) {
	.q-training__modal-action ul {
		flex-direction: row;
	}
	.q-training__modal-action ul li {
		margin-top: 0;
		margin-right: 30px;
	}
	.q-training__modal-action ul li:last-child {
		margin-top: 0;
		margin-right: 0;
	}
}
.q-training__modal-action a {
	display: block;
	text-align: center;
	color: #FFF;
	border-radius: 2px;
	padding: 17px 18px;
	min-width: 190px;
	text-decoration: none;
	font-size: 13px;
	font-weight: 300;
	letter-spacing: -0.17px;
	transition: opacity 0.3s;
}
.q-training__modal-action a:hover {
	opacity: 0.9;
}
.q-training__modal-action a span {
	display: block;
	font-size: 15px;
	font-weight: 500;
	line-height: 1.45;
	letter-spacing: -0.2px;
}
.q-training__modal-footer {
	display: flex;
	flex-direction: row;
	max-width: 31.25em;
	margin: 0;
}
.q-training__modal-footer p {
	color: #6a778b;
	font-family: Caecilia, sans-serif;
	font-size: 0.875em;
	letter-spacing: -0.009em;
	margin: 0 0 0 10px;
}
.q-training__modal-link--primary {
	background: #2E8BE0;
}
.q-training__modal-link--secondary {
	background: #6CA3CF;
}
.q-training__course-modal .fancybox-close-small {
	right: 25px;
	top: 25px;
}
.q-training__course-modal .fancybox-close-small:after {
	font-size: 45px;
	color: #FFF;
	background: transparent;
}
.q-training__tabs-section-link {
	text-decoration: none;
	position: relative;
	z-index: 1;
	display: inline;
}
.q-training__tabs-section-link:hover {
	text-decoration: none;
}
.q-training__tabs-section-link::after {
	content: "";
	height: 1px;
	width: 100%;
	background: #2E8BE0;
	position: absolute;
	top: auto;
	left: 0;
	bottom: -4px;
}
.q-training__tabs-content {
	margin-bottom: 100px;
}
.q-training__user-wrapper {
	display: flex;
	align-items: center;
	margin: 0 0 0 auto;
	justify-content: flex-end;
	margin-bottom: 20px;
	margin-top: 40px;
}
@media (min-width: 90em) {
	.q-training__user-wrapper {
		margin-top: 4px;
	}
}
.q-training__user-training {
	font-weight: 500;
	padding: 0 5px 0 7px;
	color: #44536B;
}
.q-training__user-separator {
	padding: 0 10px;
	color: #2E8BE0;
}
.q-training__user-login {
	font-weight: 500;
}
.q-training__user-login,
.q-training__user-account {
	color: #2E8BE0;
	text-decoration: none;
}
.q-training__user-login:hover,
.q-training__user-account:hover {
	color: #549EE2;
	text-decoration: none;
}

/* instructor-led training */

.table-wrapper {
	display: flex;
	flex-direction: column;
}
.table-row {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	border: 0;
	padding: 0 12px 20px;
	min-height: 56px;
	background: #F7F7F7;
	margin-bottom: 17px;
	border-radius: 2px;
}
.table-row.table-header {
	min-height: 50px;
	font-weight: 500;
	color: #44536B;
	display: none;
}
.table-column.date {
	background: #E9E7E7;
	width: calc(100% + 24px);
	margin: 0 -12px;
	border-radius: 6px 6px 0 0;
	font-weight: 500;
	letter-spacing: -0.14px;
	padding: 10px 12px;
}
.table-column.date > span {
	font-size: 15px;
	letter-spacing: -0.13px;
}
.table-column.name {
	padding: 10px 0 15px;
	flex: unset;
	width: 100%;
}
.table-column.location {
	flex: unset;
	width: 100%;
	padding: 0;
}
.table-column.action {
	width: 100%;
	padding: 20px 8px 0 0;
}
.classSignUpButton {
	display: flex;
	flex-flow: row-reverse;
}
.classSignUpButton > a,
.classSignUpButton > span {
	align-self: center;
	display: block;
	font-size: 15px;
	letter-spacing: -0.13px;
}
.classSignUpButton > a {
	background: #9BC0E0;
	color: #FFF;
	border: 1px solid #9BC0E0;
	width: 86px;
	text-align: center;
	line-height: 45px;
	margin-left: 6px;
	text-decoration: none;
	transition: all 0.3s ease-in-out;
	font-weight: 500;
}
dl.classInfo,
dl.courseInfo {
	padding-top: 0;
	padding-bottom: 0;
	margin: 0;
}
.courseInfo dt,
.classInfo dt {
	margin-bottom: 0;
}
.filterBar > div {
	background: #f7fafc;
	padding: 15px 12px;
	margin-bottom: 30px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}
.filterBar > div > label {
	font-family: Gotham, sans-serif;
	color: #44536B;
	font-weight: 500;
	font-size: 14px;
	margin-bottom: 12px;
}
.filterBar > div > select {
	width: 100%;
	max-width: 400px;
	-webkit-appearance: none;
	border-radius: 2px;
	background-color: transparent;
	background-image: url(https://ik.imagekit.io/qualys/image/form/select-arrows.png);
	background-position: calc(100% - 10px) 50%;
	background-size: 6px 15px;
	background-repeat: no-repeat;
	padding: 0 30px 0 15px;
	height: 40px;
}
.filterBar > div > select:focus {
	border: 1px solid #627281;
	outline: none;
}
.courseInfo dt,
.classInfo dt {
	color: #2E8BE0;
	font-weight: 500;
	cursor: pointer;
}
.courseInfo dt {
	font-size: 18px;
	text-decoration: underline;
	letter-spacing: -0.17px;
}
.classInfo dd,
.courseInfo dd {
	margin: 0;
	display: none;
	white-space: pre-wrap;
}
.classInfo dd p,
.courseInfo dd p {
	font-size: 0.85em;
	margin: 0;
}
.expanded dd {
	display: block;
}

@media (min-width: 480px) {
	.filterBar > div {
		flex-direction: row;
		flex-wrap: nowrap;
		align-items: center;
		padding: 15px 25px;
	}
	.filterBar > div > label {
		margin-bottom: 0;
		margin-right: 12px;
	}
}
@media (min-width: 680px) {
	.table-row.table-header {
		display: flex;
	}
	.table-row {
		flex-direction: row;
		border-top: 1px solid #6A778B;
		border-radius: unset;
		background: unset;
		padding: 6px 0;
		margin-bottom: unset;
	}
	.table-column.date {
		width: 100px;
		padding: 10px 0 10px 1em;
		margin: unset;
		font-weight: 400;
		background: unset;
		border-radius: unset;
	}
	.table-header .table-column.date {
		font-weight: 500;
	}
	.table-column.name {
		flex: 1;
		padding: 10px 1em 10px 0.5em;
		width: unset;
	}
	.courseInfo dt {
		font-size: 16px;
		letter-spacing: -0.009em;
		text-decoration: none;
	}
	.table-column.location {
		flex: 1;
		width: unset;
		padding: 10px 1em 10px 0.5em;
	}
	.table-column.action {
		width: 180px;
		padding: 0;
	}
	.table-wrapper .table-row:last-child {
		border-bottom: 1px solid #6A778B;
	}
}


.q-training__course {
	padding-bottom: 60px;
}
.q-training__course-header {
	display: flex;
	flex-flow: row;
	align-items: baseline;
	padding-bottom: 40px;
}
.q-training__library-header {
	display: flex;
	flex-flow: row;
	align-items: baseline;
	padding-bottom: 10px;
}
.q-training__course-header h3,
.q-training__library-header h3 {
	margin: 0 20px 0 0;
}
.q-training__course-row {
	display: flex;
	align-items: stretch;
	flex-wrap: wrap;
}
@media (min-width: 380px) {
	.q-training__course-row {
		margin-left: -15px;
		margin-right: -15px;
	}
}
.q-training__course-column {
	flex: none;
	height: 100%;
	padding-left: 15px;
	padding-right: 15px;
	position: relative;
	width: 100%;
}
.q-training__course-column:first-child {
	margin-bottom: 40px;
}
@media (min-width: 720px) {
	.q-training__course-column {
		width: auto;
		max-width: 50%;
		flex: 1;
	}
	.q-training__course-column:first-child {
		padding-right: 3em;
		margin-bottom: 0;
	}
	.q-training__course-column:last-child {
		flex: 1;
	}
}
.q-link {
	align-items: center;
	color: #2E8BE0;
	font-family: Gotham, sans-serif;
	font-size: 14px;
	font-weight: 500;
	line-height: 1.3;
	margin: 0;
	text-decoration: none;
	transition: color 200ms;
}
.q-link:hover {
	text-decoration: none;
	color: #1A77CC;
}
.q-link .link-arrow {
	transition: transform 200ms;
}
.q-link:hover .link-arrow {
	display: inline-block;
	transform: translate3d(5px, 0, 0);
}
.q-training__course-column ol {
	margin: 0;
	list-style-type: none;
	padding-left: 15px;
	color: #262626;
}
.q-course-handson + ol {
	color: #6A778B;
}
.q-training__course-column ol li {
	position: relative;
	line-height: 1;
}
.q-training__course-column ol li:before {
	content: "-";
	position: absolute;
	left: -15px;
}
.q-training__course-column ol li > p {
	margin: 0 0 0.65em;
	line-height: 1.45;
}
h3.q-course-title {
	font-weight: 400;
	margin: 0 0 0.85em;
}
h4.q-course-handson {
	font-weight: 400;
	margin: 0 0 1.45em;
	max-width: 22em;
	color: #6A778B;
}
.notice-red {
	color: red;
}
.heroContent p
{
	max-width: 49em;
	font-size: 20px;
	line-height:1.5;
	font-weight: 400;
}

/**
* Instructor training
*/
.q-classes-filter-container {
	display: block;
	padding: 15px 25px;
	background: #f7fafc;
	margin-top: 40px;
}
.q-classes-filter {
	display: flex;
	align-items: center;
	min-height: 40px;
}
.q-classes-filter label {
	padding-right: 15px;
	font-weight: 500;
	color: #44536B;
	font-size: 14px;
}
.q-classes--list {
	width: 100%;
	max-width: 400px;
	-webkit-appearance: none;
	border-radius: 2px;
	background-color: transparent;
	background-image: url(https://ik.imagekit.io/qualys/image/form/select-arrows.png);
	background-position: calc(100% - 10px) 50%;
	background-size: 6px 15px;
	background-repeat: no-repeat;
	padding: 0 30px 0 15px;
	height: 40px;
}
.q-classes-table-container {
	margin-top: 20px;
}
.q-course-row {
	flex-flow: column;
	border-bottom: 0;
	background: #F7F7F7;
	margin-bottom: 20px;
	border-radius: 4px;
}
