	.q-classes-table-container {
		margin-top: 40px;
	}
	.q-course-row {
		display: flex;
		flex-flow: row;
		align-items: flex-start;
		border-bottom: 1px solid;
		background-color: #FFF;
		border-radius: 0;
		margin-bottom: 0;
	}
}
.q-handle {
	height: 40px;
	width: 40px;
	margin-right: 10px;
	cursor: pointer;
	display: flex;
	align-items: center;
	text-align: center;
}
.q-handle > img {
	margin: 0 auto;
}
.q-course-row--head {
	display: none;
}
@media (min-width: 920px) {
	.q-course-row--head {
		display: flex;
		border-top: 1px solid;
		cursor: default;
		min-height: 50px;
		align-items: center;
	}
}
.q-course-col {
	text-align: left;
	padding: 8px 15px;
	width: 100%;
	max-width: unset;
}
.q-course-col > span {
	user-select: none;
}
.q-course-row--head .q-course-col > span {
	cursor: default;
	display: block;
	min-height: unset;
}
@media (min-width: 920px) {
	.q-course-col {
		padding: 8px;
	}
	.q-course-col > span {
		display: flex;
		min-height: 46px;
		align-items: center;
	}
}
.q-course-col strong {
	font-weight: 500;
	color: #44536B;
}
.class-col > span {
	color: #2E8BE0;
	font-weight: 500;
}
.location-col {
	flex: 1;
}
.location-col > span {
	color: #2E8BE0;
	font-weight: 500;
}
.action-col {
	text-align: right;
}
.date-col {
	background: #E9E7E7;
	border-radius: 4px 4px 0 0;
	font-weight: 500;
}
@media (min-width: 920px) {
	.date-col {
		max-width: 100px;
		width: 10%;
		background: unset;
		border-radius: unset;
		font-weight: 400;
	}
	.class-col {
		max-width: 520px;
		width: 40%;
	}
	.action-col {
		max-width: 240px;
		width: 30%;
		padding-right: 0;
	}
}
.q-course-action--items {
	display: inline-flex;
	align-items: center;
	align-self: flex-end;
	user-select: none;
}
.q-course-type {
	margin-right: 10px;
}
.course-desc {
	font-size: 14px;
	display: none;
}
a.q-course-action {
	background: #9BC0E0;
	color: #FFF;
	border: 1px solid #9BC0E0;
	width: 86px;
	text-align: center;
	line-height: 45px;
	margin-left: 6px;
	text-decoration: none;
	transition: all 0.3s ease-in-out;
	font-weight: 500;
	border-radius: 2px;
}
@media (min-width: 920px) {
	a.q-course-action {
		border-radius: 0;
	}
}
.q-course-row--desc {
	display: none;
	align-items: flex-start;
}
.q-course-row--desc .q-course-col {
    padding: 0;
}
.q-course-row--desc .course-desc {
	padding: 0 15px 0 8px;
}
.q-course-row--desc .course-desc p {
	margin-top: 0;
}
.q-course-row.expand .course-desc {
	display: block;
}
.inline-link {
    text-decoration: underline !important;
	color: #fff !important;
	font-weight: 500;
}
.q-content-hero__hero-top_content {
	max-width: 55em;
    font-size: 18px;
    line-height: 1.5;
    font-weight: 400;
	background: #ED2E26;
	padding: 20px 30px;
	margin-bottom: 40px;
	color: #fff;
	margin-top: 40px;
}
.q-search__container {
    margin-bottom: 0px;
